.TodoItem {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 14px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    color: aquamarine;
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 28px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
    text-decoration-color: red; /* Cambia el color de la l√≠nea */

  }

  