form {
    width: 50%;
    max-width: 50%;
    background-image: url("https://w0.peakpx.com/wallpaper/286/197/HD-wallpaper-tatsumaki-neon-one-punch-man-character-black-simple-opm.jpg");
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 50%;
    background-size: cover; /* Hace que la imagen cubra toda la pantalla */
    background-position: center; /* Centra la imagen */
    background-repeat: no-repeat; /* Evita que la imagen se repita */
    height: 100%; /* Asegura que cubra el 100% de la altura de la pantalla */
    margin: 0;
    background-color: black;
  }
  
  label {
    text-align: left;
    font-size: 25px;
    color: darkcyan;
    margin-bottom: 26px;
    font-family: fantasy;
  }
  
  textarea {
    background-color: transparent;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: aqua;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button--add {
    background: black;
    color: #61DAFA;
  }

  .TodoForm-button--add:hover {
    color: greenyellow;
  }
  
  
  .TodoForm-button--cancel {
    background: black;
    color: #61DAFA;
  }

  .TodoForm-button--cancel:hover {
    color: red;
  }